<template>
  <v-form @submit.prevent="submit" v-model="valid" class="mx-auto ">
    <h2 class="text-center mb-3">AD帳號註冊</h2>

    <div v-for="(value, key) in info" :key="key" class="label-container text-start mt-3">
      <span class="label-item">{{ getLabel(key) }}</span> <span class="label-item">{{ value }}</span>
    </div>

    <v-text-field hide-details="auto" v-model="form.reason" density="compact" label="申請原因"
      prepend-inner-icon="mdi-information" variant="solo" required class="register-item mb-2">
      <template #label>
        <!-- <span class="text-danger">*</span> -->
        申請原因
      </template>
    </v-text-field>

    <v-btn type="submit" variant="flat" color="primary" block class="mt-5" :loading="loading">
      註冊
    </v-btn>
    <div class="or">或</div>
    <router-link to="/sign-in" class="text-decoration-none">回登入</router-link>
  </v-form>
</template>

<script setup>
import { onMounted } from "vue";
import { users } from "@/requests";

const router = useRouter();
const loading = ref(false);
const form = ref({
  email: "",
  name: "",
  phone: "",
  unit: "",
  reason: "",
});
const info = ref({
  email: "",
  name: "",
  phone: "",
  unit: "",
});
onMounted(async () => {
  let { data } = await users.get("7a84fc32-378c-4d67-8537-f401b18881ec");
  info.value.email = data.email;
  info.value.name = data.name;
  info.value.phone = data.phone;
  info.value.unit = data.unit;
});

const labels = {
  email: "帳號/信箱",
  name: "姓名",
  phone: "電話",
  unit: "單位",
};

const getLabel = (key) => labels[key] || key;

const submit = async () => {
  loading.value = true;
  //呼叫送出API還沒做
  router.push('/sign-in/registerSuccess');
};
</script>
<style scoped>
.register-item {
  border-radius: 10px !important;
  margin-top: 18px;
}

.register-item .v-field {
  height: 25px;
}

.register-item .v-input__control {
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.5) !important;
  background-color: white;
}

:global(.right .register-item .v-input__details .v-messages__message) {
  color: lightcoral !important;
}

:global(.register-item .v-field) {
  border-radius: 10px !important;
}

:global(.right .register-item .v-input__control *) {
  color: gray !important;
}

:global(.right .register-item .text-danger) {
  color: red !important;
}

.label-container {
  display: flex;
  justify-content: space-between;
  min-width: 280px;
}

.label-item {
  flex: 1;
  min-width: 100px;
}
</style>